import React, { Component } from 'react';
import "./RoomSlider.css";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import img1 from "./images/experience/snorkling/snorkling-1.JPG"
import img2 from "./images/experience/snorkling/snorkling-2.JPG"
import img3 from "./images/experience/snorkling/snorkling-3.JPG"
import img4 from "./images/experience/snorkling/snorkling-4.JPG"
import AOS from "aos";

AOS.init({
  once: false,
  duration: 1000,
  offset: 300,
});

export class DoubleRoomSlider extends Component {
  render() {
    return (
      <div   data-aos="fade-up" data-aos-delay="100">
         <Carousel
        autoPlay={true}
        infiniteLoop={true}
        interval={2500} 
        stopOnHover={false}
        emulateTouch={true}
        dynamicHeight={true} // centerMode={true}
        showStatus={false}>
                <div>
                    <img src={img1} />
                </div>
                <div>
                    <img src={img2} />
                </div>
                <div>
                    <img src={img3} />
                </div>
                <div>
                    <img src={img4} />
                </div>
              
            </Carousel>
      </div>
    )
  }
}

export default DoubleRoomSlider